// Mock `widget` object
if (!window.widget) {
  console.log("Mock widget created");
  const Widget = function () {
    this.uwaUrl = window.location.origin + "/"; 
    console.log("widget.uwaUrl:", this.uwaUrl); // Ensure uwaUrl is set correctly
    let events = {};
    this.addEvent = (event, callback) => {
      events[event] = callback;
      if (event === "onLoad") {
        if (document.readyState === "loading") {
          window.addEventListener("DOMContentLoaded", callback);
        } else {
          callback();
        }
      }
    };
    this.setTitle = (title) => {
      document.title = title;
    };
    this.dispatchEvent = () => {};
 
  };

  window.widget = new Widget();

} else {
  console.log("Real widget object detected. Skipping mock initialization.");
}

// Mock `define` function for AMD modules
if (typeof define === "undefined") {
  window.define = (name, deps, callback) => {
    if (typeof deps === "function") {
      callback = deps;
    }
    callback();
  };
}

// Mock `__webpack_public_path__`
if (typeof __webpack_public_path__ === "undefined") {
  window.__webpack_public_path__ = "./";
}

if (document.readyState === "loading") {
  console.log("Waiting for DOM readiness..."); // Log added
  window.addEventListener("DOMContentLoaded", () => {
    console.log("DOM is ready"); // Log added
  });
} else {
  console.log("DOM is already ready"); // Log added
}


if (window.widget) {
  console.log("Simulating onLoad event manually"); // Log added
  window.widget.addEvent("onLoad", () => {
    console.log("Manually triggered onLoad event"); // Log added
  });
}


console.log("Mock environment initialized");