import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import './setupMocks'; // Add mocks at the top
import { initWidget } from './lib/widget';

// Dynamically set Webpack's public path if `widget` is available
if (window.widget && window.widget.uwaUrl) {
  __webpack_public_path__ = window.widget.uwaUrl.substring(0, window.widget.uwaUrl.lastIndexOf("/") + 1);
}

function start() {
  const rootElement = document.getElementById('root');
  if (!rootElement) {
    console.error("Root element not found. Ensure there is a #root in your HTML.");
    return;
  }
  const root = ReactDOM.createRoot(rootElement);
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}

export function initializeWidget() {
  initWidget((widget) => {
    console.log("Widget initialized"); // Add this log
    widget.addEvent("onLoad", () => {
      console.log("onLoad event triggered"); // Add this log
      start();
    });

    widget.addEvent("onRefresh", () => {
      console.log("onRefresh event triggered"); // Add this log
    });
  });
}

console.log("Starting widget initialization...");
initializeWidget(); // Invoke the function here to initialize the widget

// Measure performance in your app (optional)
reportWebVitals();
